html {
  height: 100%;
  
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
 background-color: transparent;
  transition: all;
  width: 100vw;
}

:root {
  --primaryColor: orange;
  --mainWhite: #fff;
  --offWhite: #f7f7f7;
  --mainBlack: white;
  --mainGrey: #ececec;
  --darkGrey: #cfcfcf;
  --mainTransition: all 0.3s linear;
  --mainSpacing: 3px;
  --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
}

/* globals */

html {
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
  
  background-repeat: no-repeat;
  background-size: 5000px 5000px;

}

.loading {
  position: absolute!important;
  top: -7%!important;
  left: 7% !important;
}

.carousel-item {
  text-align: center;
  width: 100vw;
}

.carousel-item img {
  height: 100vh;
  justify-content: center;
  align-items: center;
  min-width: 100vw;
}

body {
  background: transparent!important;
  font-family: "Roboto", sans-serif;
  line-height: 1.4;
}

h1 {
  font-size: 3em;
  line-height: 1;
  margin-bottom: 0.5em;
}

h2 {
  font-size: 2em;
  margin-bottom: 0.75em;
}

h3 {
  font-size: 1.5em;
  line-height: 1;
  margin-bottom: 1em;
}

h4 {
  font-size: 1.2em;
  line-height: 1.25;
  margin-bottom: 1.25em;
}

h5 {
  font-size: 1em;
  font-weight: bold;
  margin-bottom: 1.5em;
}

h6 {
  font-size: 1em;
  font-weight: bold;
  margin-bottom: 1.5em;
}

.btn-primary {
  display: inline-block;
  text-decoration: none!important;
  letter-spacing: var(--mainSpacing);
  color: black!important;
  background: var(--primaryColor)!important;
  padding: 0.4rem 0.9rem;
  border: 3px solid var(--primaryColor)!important;
  transition: var(--mainTransition);
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 700;
  align-items: center;
  padding-top: 0.5rem;
}

.btn-primary:hover {
  background: transparent!important;
  color: var(--primaryColor)!important;
}

.loading {
  text-transform: capitalize;
  text-align: center;
  margin-top: 3rem;
}

.error {
  text-align: center;
  text-transform: uppercase;
  margin: 8rem 0;
  color: black;
}

.empty-search {
  text-align: center;
  text-transform: capitalize;
  margin: 2rem 0;
  padding: 1rem;
  letter-spacing: var(--mainSpacing);
}

/* end of globals */

/* Navbar */

/* end of navbar */

/* Hero */

.defaultHero, .roomsHero {
  min-height: calc(100vh - 66px)!important;
  background: url("./images/7433.jpg") center/cover no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.roomsHero {
  background-image: url("./images/31335.jpg");
  min-height: 60vh;
}

/* End of Hero */

/* Banner */

.banner {
  display: inline-block;
  background: rgba(0, 0, 0, 0.7);
  color: var(--mainWhite);
  padding: 2rem 1rem;
  text-align: center;
  text-transform: capitalize;
  letter-spacing: var(--mainSpacing);
  border-radius: 20px;
  box-shadow: 0px 0px 1px black;
  position: absolute;
  z-index: 1!important;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.banner h1 {
  font-size: 1.5rem;
}

.banner div {
  width: 10rem;
  height: 5px;
  background: var(--primaryColor)!important;
  margin: 1.7rem auto;
  border-radius: 20px;
}

.banner p {
  font-size: 1rem;
  margin-bottom: 1rem;
}

.btn-primary {
  border-radius: 20px;
  font-weight: 400;
  font-size: 1rem;
}

@media screen and (min-width: 576px) {
  .banner {
    padding: 2rem 3rem;
  }
  .banner h1 {
    font-size: 3rem;
  }
}

@media screen and (min-width: 992px) {
  .banner {
    padding: 2rem 6rem;
  }
  .banner h1 {
    font-size: 4rem;
  }
}

/* End of Banner */

/* Title */

.section-title {
  text-align: center;
  margin-bottom: 4rem;
  color: white;
}

.section-title h4 {
  font-size: 2rem;
  letter-spacing: var(--mainSpacing);
  text-transform: capitalize;
  margin-bottom: 1rem;
  color: white;
}

.section-title div {
  width: 5rem;
  height: 5px;
  margin: 0 auto;
  background: var(--primaryColor);
  color: white;
}

/* end of Title */

